        p Es un tipo de diseo creado por Google, establecido desde junio de 2014, orientado especficamente para aplicaciones nativas Android desde la versin Lollipop. Se ha publicado una gua (disponible en 
          |
          a(href="https://material.io/design" target="_blank") https://material.io/design
          |) para que se realicen aplicaciones web; los tems propuestos son:
        ul.lista-ul.mb-0
