    p.mb-5(data-aos="fade-right") Se presentan tambin los tres principios fundamentales:
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul1")(data-aos="fade-down")
      .row(titulo="A. Lo material como una metfora")
        .col-12
          p Como lo material est unido a la realidad, fusionndose con el espacio, el tiempo y el movimiento, est ligado a la realidad tctil, es por esto que se puede integrar con la tecnologa, haciendo uso de la creatividad y la imaginacin.
          p Los bordes y superficies de las cosas materiales entregan pistas visuales basadas en la realidad. El uso de atributos familiares al mundo real ayuda al usuario a entender las posibilidades de lo que se puede hacer con cada elemento presente en la interfaz. Para lograr esto, se deben presentar los principios superficie, luz y movimiento, como elementos principales para transmitir cmo los objetos existen y estn dispuestos a interactuar uno con el otro o con el usuario.
          .titulo-sexto.color-primario.mb-4
            p.mb-0 #[b Figura 3]. Materiales
          figure
            img(src='@/assets/curso/tema2/img7.svg', style="width: 160px" ,alt='Imagen alusiva a los materiales.')
      .row(titulo="B. Llamativo e intencional")
        .col-12
          p Todos los elementos visuales son diseados por los conceptos de la teora del diseo, como el uso del color, los espacios en blanco, sombras, el sistema de tabla de datos, etc. Todos estos elementos ayudan a determinar la jerarqua y lectura visual.
          .titulo-sexto.color-primario.mb-4
            p.mb-0 #[b Figura 4]. Diseo de componentes
          figure.mb-4
            img(src='@/assets/curso/tema2/img8.svg', style="width: 460px" ,alt='Imagen alusiva al diseo de componentes, la cual contiene la siguiente informacin: + Button Button')
          p El sistema de color de Material Design ayuda a aplicar color a las interfaces de usuario de una manera simplificada. En este sistema, el diseador define un color como primario y otro como secundario, para representar el tema de su marca. Las variaciones de oscuras y claras para cada color se aplican a la interfaz de usuario de diferentes formas de manera preestablecida.
          .titulo-sexto.color-primario.mb-4
            p.mb-0 #[b Figura 5]. Manejo de colores
          figure
            img(src='@/assets/curso/tema2/img9.svg', style="width: 460px" ,alt='Imagen alusiva al maneo de colores, la cual contiene diferentes tonalidades.')
      .row(titulo="C. El movimiento otorga significado")
        .col-12
          p En una aplicacin web o mvil, una animacin capta la atencin de los usuarios, sin embargo, es deseable que estas animaciones no interrumpan la experiencia de usuario. De esta forma, es deseable que exista relacin entre las animaciones y la propia esencia del sitio web o aplicacin, por ejemplo, el efecto de presionar el botn, la experiencia real de desplazar un elemento.
          p.mb-0 Para Google, el movimiento enfatiza el papel del usuario como el actor principal de la interaccin con la mquina y el que dirige los movimientos en dicha interaccin. Las acciones del usuario tienen el poder de cambiar el diseo o disposicin de los elementos. Es as que los elementos de la interfaz son presentados al usuario sin interferir con la experiencia de usuario, incluso cuando dichos elementos se transforman y reorganizan.
    p.mb-5(data-aos="fade-right") En conclusin, Material Design es una propuesta de diseo novedosa y que se est incorporando en las nuevas versiones de Android. Los principios del Material Design van guiados a mejorar la experiencia de usuario teniendo en cuenta las ltimas tecnologas. Tenga en cuenta:
    figure.d-none.d-lg-block.mb-5(data-aos="fade-left")
      img(src='@/assets/curso/tema2/img10.svg', style="width: 1060px" ,alt='Imagen que explica el proceso de Material Design, el cual consiste en lo siguiente: Validacin de interfaz de usuario. La validacin y verificacin de la interfaz del usuario es recomendable que se realice en tres puntos del proceso, que son: En el anlisis: las interfaces deben responder a los requisitos funcionales definidos en el proceso de captura y anlisis de requisitos. En el diseo: las interfaces diseadas deben garantizar la calidad en la presentacin, esto es: que sea fcilmente identificable la funcionalidad de cada componente presente, en especial, aquellos que estn dispuestos para lograr la interaccin con el usuario. En la funcionalidad: durante las pruebas, donde bsicamente se prueba ejecutando la aplicacin, de lo que tratarn otras unidades temticas.').m-auto
    figure.d-block.d-lg-none.mb-5(data-aos="fade-left")
      img(src='@/assets/curso/tema2/img10m.svg', style="width: 320px" ,alt='Imagen que explica el proceso de Material Design, el cual consiste en lo siguiente: Validacin de interfaz de usuario. La validacin y verificacin de la interfaz del usuario es recomendable que se realice en tres puntos del proceso, que son: En el anlisis: las interfaces deben responder a los requisitos funcionales definidos en el proceso de captura y anlisis de requisitos. En el diseo: las interfaces diseadas deben garantizar la calidad en la presentacin, esto es: que sea fcilmente identificable la funcionalidad de cada componente presente, en especial, aquellos que estn dispuestos para lograr la interaccin con el usuario. En la funcionalidad: durante las pruebas, donde bsicamente se prueba ejecutando la aplicacin, de lo que tratarn otras unidades temticas.').m-auto
    p(data-aos="fade-right") Los objetivos que se obtienen con las pruebas de interfaz son:
    .row.align-items-center.mb-5
      .col-lg-8.mb-4.mb-lg-0(data-aos="fade-right")
        ul.lista-ul.mb-0
          li.d-flex
            i.fas.fa-check(style="color: #451484")
            |
            p.mb-0 Las caractersticas que tiene la interfaz deben probarse para asegurar que las reglas del diseo, la esttica y el contenido visual estn a disposicin del usuario sin lugar a errores.
          li.d-flex
            i.fas.fa-check(style="color: #451484")
            |
            p.mb-0 Los elementos individuales de la interfaz deben probarse de forma unitaria (por ejemplo, se prueba HTML dinmico, cgi, carrito de compras, etc.) y, con especial cuidado, aquellos que manipulan la presentacin de datos y componentes visuales.
          li.d-flex
            i.fas.fa-check(style="color: #451484")
            |
            p.mb-0 La interfaz debe probarse frente a cada historia de usuario o caso de uso, para descubrir errores semnticos y facilidad de uso por parte del usuario.
          li.d-flex
            i.fas.fa-check(style="color: #451484")
            |
