    p.mb-5(data-aos="fade-right") Para entender esto un poco ms, creemos el siguiente archivo.
    .titulo-sexto.color-acento-contenido.mb-4(data-aos="fade-down")
      p.mb-0 #[b Figura 6]. Creacin mdulo matemticas en Node.js
    .row.mb-5
