          .col-lg-6(data-aos="fade-left")
            p.mb-0 A diferencia de los archivos js de JavaScript, en Node.js no es requerido un documento HTML para ser ejecutado, sino, por el contrario, se usa el intrprete de Node.js. En el archivo creado, se definirn algunas funciones bsicas y constantes.
    .titulo-sexto.color-acento-contenido.mb-4(data-aos="fade-down")
      p.mb-0 #[b Figura 7]. Definicin de funciones y constantes en mdulo Node.js
    .row.mb-5
      .col-12.bg-c3.p-4
        .row.justify-content-center.align-items-center
          .col-lg-4.mb-4.mb-lg-0(data-aos="fade-right")
            p matematicas.js
            pre
              code.language-javascript
                |1  const PI = 3.1416
                |2  
                |3  function sumar(x1,x2) {
                |4    return x1+x2
                |5  }
                |6  
                |7  function restar(x1,x2) {
                |8    return x1-x2
                |9  }
                |10 
                |11 exports.PI = PI
                |12 exports.sumar = sumar
                |13 exports.restar = restar
          .col-lg-6(data-aos="fade-left")
            p.mb-0 En la parte inferior de la definicin del mdulo, se puede observar que se exportan los elementos, tales como las funciones y las constantes. Luego de definido el mdulo, este podr ser usado en otra seccin del cdigo, es decir, todo lo que fue exportado podr ser reutilizado; si se requiere de alguna funcin interna del mdulo y que no sea visible en otras secciones, solo basta con no exportarlo, pero podr ser usado al interior del mdulo en otra funcin.
    .titulo-sexto.color-acento-contenido.mb-4(data-aos="fade-down")
