          .col-lg-6(data-aos="fade-left")
            p.mb-0 En el archivo example.js, se realiza la importacin del mdulo construido, llamado matematicas.js, en el cual se podr hacer uso de las funcionalidades exportadas, empleando, de esta manera, el concepto de reutilizacin de cdigo.
    .titulo-sexto.color-acento-contenido.mb-4(data-aos="fade-down")
