            p.mb-0 Tambin se pueden consumir mdulos definidos en el #[i core] de Node.js; entre estos mdulos, se pueden destacar los siguientes: os, http, fs, net, url, path, dns, process, entre otros ms.
    .titulo-sexto.color-acento-contenido.mb-4(data-aos="fade-down")
