    p.mb-5(data-aos="fade-right") Hasta el momento, se han tratado temas relacionados con aplicaciones ejecutadas desde el compilador, apoyadas en la consola CMD. Siga las siguientes instrucciones para construir el primer proyecto tipo servidor con pginas web estticas. Detalle los pasos que se presentan a continuacin.
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul3")(data-aos="fade-down")
