    .titulo-segundo.color-acento-botones
      h2 2.4 Codificacin de peticin tradicional HTTP, sncrona
    .row.align-items-center.mb-5
      .col-lg-4.mb-4.mb-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema2/img23.png', style="width: 390px" ,alt='').m-auto
      .col-lg-8(data-aos="fade-left")
        p El desarrollo de aplicaciones sincrnicas presenta una serie de limitaciones, teniendo en cuenta las capacidades de lo que presenta la red, por lo cual es determinante la aplicacin de tecnologas alternativas. Por ejemplo, imagine una aplicacin relacionada con el mercado burstil donde se plasman los valores actuales del mercado al momento de ingresar la aplicacin, una aplicacin sincrnica mostrara esta informacin de manera indefinida hasta que una accin de usuario permita realizar la actualizacin de forma manual. 
        p.mb-0 Lo mismo pasa en un chat. Imagine que el chat de Facebook es sincrnico; no sera posible ver, en tiempo real, las respuestas de las personas con las cuales estamos trabajando, bsicamente, se requerira que actualizsemos el chat para poder ver si realmente ha llegado un nuevo mensaje. Este tipo de interacciones ha sido reemplazado por funcionalidades asincrnicas, que, posiblemente x cantidad de tiempo, permiten una actualizacin de la informacin.
    .row.mb-5(data-aos="fade-down")
      .col-auto.px-3.py-2.bg-c4
        .row.align-items-center
          .col-auto.pe-0
            figure
              img(src='@/assets/curso/tema1/img12.svg', style="width: 5px" ,alt='').m-auto
          .col
            h3.mb-0 Codificacin de peticin tradicional HTTP, asncrona
    p.mb-5(data-aos="fade-right") Las aplicaciones asncronas, por su diseo, permiten notificar cambios al cliente cuando en el servidor han ocurrido cambios, de esta forma, permite que la actualizacin de informacin sea ms eficiente y rpida. Para la implementacin de un flujo asncrono, es posible encontrar tres formas:
    .titulo-sexto.color-acento-contenido.mb-4(data-aos="fade-down")
      p.mb-0 #[b Figura 13]. Ejecucin de flujo poll
    AcordionA.mb-3(tipo="a" clase-tarjeta="tarjeta tarjeta--azul1")(data-aos="fade-down")
      .row.justify-content-center(titulo="Comunicacin asncrona POLL")
        .col-lg-10
          p Consiste en que el cliente enva peticiones http al servidor en tiempos regulares, es decir, cada x cantidad de tiempo, para conocer el estado de los datos en el servidor y representar los cambios evidenciados. Un ejemplo de estos es el uso de la funcin setInterval y Ajax del lado de JavaScript.
          figure
            img(src='@/assets/curso/tema2/img21.svg', style="width: 1100px" ,alt='Imagen que muestra el proceso de la comunicacin asncrona POLL.').m-auto
      .row.justify-content-center(titulo="Comunicacin asncrona LONG-POLL")
        .col-lg-10
          p Consiste en que el cliente enva una peticin http al servidor, el cual, cuando tiene una actualizacin, genera una respuesta, e inmediatamente se vuelve a generar una nueva peticin, es decir, que si se enva una peticin, esta queda encolada, hasta que haya una respuesta de actualizacin; al recibir la respuesta, se repite el proceso. Es an ms ptima que el mtodo POLL, dado que este enva una serie de peticiones con una frecuencia determinada, haya o no cambios, mientras que LONG-POLL solo lo hace si una peticin ha sido resuelta, y espera que haya un nuevo cambio.
          figure
            img(src='@/assets/curso/tema2/img22.svg', style="width: 1100px" ,alt='Imagen que muestra el proceso de la comunicacin asncrona LONG-POLL.').m-auto
      .row.justify-content-center(titulo="Comunicacin asncrona PUSH")
        .col-lg-10
          p Consiste en que el cliente enva una peticin al servidor http, el cual genera una respuesta que nunca termina, es decir, crea una conexin que no termina y queda abierta, de esta manera, permite enviar submensajes de actualizacin hacia el cliente. Mientras la conexin est abierta, el servidor se encarga de enviar mensajes de PING para evitar que el cliente realice un cierre de la conexin por timeout. Los sockets son un ejemplo de este tipo de conexiones.
          figure
            img(src='@/assets/curso/tema2/img23.svg', style="width: 1100px" ,alt='Imagen que muestra el proceso de la comunicacin asncrona PUSH.').m-auto
    p.ms-2.mb-5(data-aos="fade-right") #[b Nota]. Tomado de: 
      |
      a(href="https://hvalls.dev/posts/polling-long-polling-push" target="_blank") https://hvalls.dev/posts/polling-long-polling-push
    p.mb-0(data-aos="fade-right") El desarrollo de aplicaciones asncronas permite una interaccin ms natural entre el usuario y el producto, permitiendo as una respuesta ms eficiente.
