    .titulo-sexto.color-acento-contenido.mb-4(data-aos="fade-down")
      p.mb-0 #[b Figura 16]. Cdigo fuente de archivo de control index.js
    .row.mb-5
      .col-12.bg-c3.p-4
        .row.justify-content-center.align-items-center
          .col-lg-8.mb-4(data-aos="fade-right")
            pre
              code.language-javascript
                |1  const app = require('express')();
                |2  const http = require('http').Server(app);
                |3  const io = require('socket.io')(http);
                |4  const port = process.env.PORT || 3000;
                |5
                |6  io.on('connection', (socket)) => {
                |7    socket.on('chat message', msg => {
                |8      io.emit('chat message', msg);
                |9    });
                |10 });
                |11
                |12 http.listen(port, () => {
                |14   console.log('Socket.IO server running at http://localhost:${port}/');
                |15 });
          .col-12(data-aos="fade-left")
            p.mb-0 Como se puede observar en la anterior imagen, en la lnea 6, se hace uso del objeto #[i io], el cual pertenece al mdulo de #[i socket.io], y permite establecer el servidor socket; en la lnea 7, establece la conexin de este; y en la lnea 8, emite el evento para que los suscritos al socket lo puedan recibir.
