        p Es requerido que, para hacer una conexin a un servidor socket, se cree un cliente socket; se requiere que esta implementacin se desarrolle del lado del cliente, es decir, ser necesario desarrollar una pgina HTML y, por medio de esta estructura, hacer uso de las facultades de JavaScript del lado del cliente.
        p.mb-0 Para ello, ser necesario crear un archivo esttico en la raz del proyecto, con el contenido HTML, y transcribir el siguiente cdigo fuente.
    .row.bg-c11
      .col-12
        .px-5
          .row.mb-3
            .col-12(data-aos="fade-right")
              pre
                code.language-html
                  | &lt;!DOCTYPE html&gt;
                  | &lt;html&gt;
                  |   &lt;head&gt;
                  |     &lt;title&gt;Socket.IO chat&lt;/title&gt;
                  |     &lt;style&gt;
                  |       body { margin: 0; padding-bottom: 3rem; font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif; }
                  |       #form { background: rgba(0, 0, 0, 0.15); padding: 0.25rem; position: fixed; bottom: 0; left: 0; right: 0; display: flex; height: 3rem; box-sizing: border-box; backdrop-filter: blur(10px); }
                  |       #input { border: none; padding: 0 1rem; flex-grow: 1; border-radius: 2rem; margin: 0.25rem; }
                  |       #input:focus { outline: none; }
                  |       #form > button { background: #333; border: none; padding: 0 1rem; margin: 0.25rem; border-radius: 3px; outline: none; color: #fff; }
                  |       #messages { list-style-type: none; margin: 0; padding: 0; }
                  |       #messages &gt; li { padding: 0.5rem 1rem; }
                  |       #messages &gt; li:nth-child(odd) { background: #efefef; }
                  |     &lt;/style&gt;
                  |   &lt;/head&gt;
                  |   &lt;body&gt;
                  |     &lt;ul id="messages"&gt;&lt;/ul&gt;
                  |       &lt;form id="form" action=""&gt;
                  |         &lt;input id="input" autocomplete="off" /&gt;&lt;button&gt;Send&lt;/button&gt;
                  |       &lt;/form&gt;
